<template>
  <div id="search-section">
    <header>
      <img @click="gohome" src="../../assets/imgs/images/pre.png" />
      <form action="/">
        <van-search
          v-model="inputvalue"
              show-action
              @input="setSearchListShow"
          placeholder="请输入搜索关键词"
        >
          <template #action>
            <div @click="startSearch">搜索</div>
          </template>
        </van-search>
      </form>
    </header>
    <div class="section">
      <div class="search-list-show">
        <div class="search-list-item" v-if="searchList.Guest ? true : false">
          <div class="search-item-title">
            <h3>读诗人</h3>
            <span @click="searchMoreList(4)"
              >更多读诗人<img src="../../assets/imgs/images/next.png"
            /></span>
          </div>
          <div class="searhc-item">
            <SearchItem
              class="search-item-com"
              v-for="item in searchList.Guest"
              :key="item.id"
              :id="item.id"
              :pic="item.Image"
              :title="item.Title"
              :SecondTitle="item.SecondTitle"
              :readingBook="true"
              :type="4"
              :isVipFree="item.isVipFree ?? false"
              :freeType="item.freeType ?? false"
              :value="inputvalue"
            ></SearchItem>
          </div>
        </div>
        <div class="search-list-item" v-if="searchList.Poem ? true : false">
          <div class="search-item-title">
            <h3>读本</h3>
            <span @click="searchMoreList(7)"
              >更多读本<img src="../../assets/imgs/images/next.png"
            /></span>
          </div>
          <div class="searhc-item-poem">
            <PoemBook
              class="search-item-book"
              v-for="(item, i) in searchList.Poem"
              :key="item.id"
              :id="item.id"
              :itemindex="i + 1 < 10 ? '0' + (i + 1) : i + 1"
              :title="item.Title"
              :author="item.author_name"
              :SecondTitle="item.SecondTitle"
              :type="7"
              :isVipFree="item.isVipFree ?? false"
              :freeType="item.freeType ?? false"
              :value="inputvalue"
            ></PoemBook>
          </div>
        </div>
        <div class="search-list-item" v-if="searchList.PoemData ? true : false">
          <div class="search-item-title">
            <h3>节目</h3>
            <span @click="searchMoreList(1)"
              >更多节目<img src="../../assets/imgs/images/next.png"
            /></span>
          </div>
          <div class="searhc-item">
            <SearchItem
              class="search-item-com"
              v-for="item in searchList.PoemData"
              :key="item.id"
              :id="item.id"
              :pic="item.Image"
              :title="item.Title"
              :SecondTitle="item.SecondTitle"
              :isVipFree="item.isVipFree ?? false"
              :freeType="item.freeType ?? false"
              :type="1"
              :value="inputvalue"
            ></SearchItem>
          </div>
        </div>
        <div
          class="search-list-item"
          v-if="searchList.SecondAlbum ? true : false"
        >
          <div class="search-item-title">
            <h3>专栏</h3>
            <span @click="searchMoreList(3)"
              >更多专栏<img src="../../assets/imgs/images/next.png"
            /></span>
          </div>
          <div class="searhc-item">
            <SearchItem
              class="search-item-com"
              v-for="item in searchList.SecondAlbum"
              :key="item.id"
              :id="item.id"
              :pic="item.Image"
              :title="item.Title"
              :SecondTitle="item.SecondTitle"
              :isVipFree="item.isVipFree ?? false"
              :freeType="item.freeType ?? false"
              :type="3"
              :value="inputvalue"
            ></SearchItem>
          </div>
        </div>
        <div class="search-list-item" v-if="searchList.Album ? true : false">
          <div class="search-item-title">
            <h3>大专栏</h3>
            <span @click="searchMoreList(2)"
              >更多大专栏<img src="../../assets/imgs/images/next.png"
            /></span>
          </div>
          <div class="searhc-item">
            <SearchItem
              class="search-item-com"
              v-for="item in searchList.Album"
              :key="item.id"
              :id="item.id"
              :pic="item.Image"
              :title="item.Title"
              :SecondTitle="item.SecondTitle"
              :isVipFree="item.isVipFree ?? false"
              :freeType="item.freeType ?? false"
              :value="inputvalue"
              :type="2"
            ></SearchItem>
          </div>
        </div>
        <div class="search-list-item" v-if="searchList.Video ? true : false">
          <div class="search-item-title">
            <h3>视频</h3>
            <span @click="searchMoreList(5)"
              >更多视频<img src="../../assets/imgs/images/next.png"
            /></span>
          </div>
          <div class="searhc-item">
            <SearchItem
              class="search-item-com"
              v-for="item in searchList.Video"
              :key="item.id"
              :id="item.id"
              :pic="item.Image"
              :title="item.Title"
              :SecondTitle="item.SecondTitle"
              :isVipFree="item.isVipFree ?? false"
              :freeType="item.freeType ?? false"
              :value="inputvalue"
              :type="5"
            ></SearchItem>
          </div>
        </div>
        <div class="search-list-item" v-if="searchList.Activity ? true : false">
          <div class="search-item-title">
            <h3>栖息岛</h3>
            <span @click="searchMoreList(6)"
              >更多栖息岛<img src="../../assets/imgs/images/next.png"
            /></span>
          </div>
          <div class="searhc-item">
            <SearchItem
              class="search-item-com"
              v-for="item in searchList.Activity"
              :key="item.id"
              :id="item.id"
              :pic="item.Image"
              :title="item.Title"
              :SecondTitle="item.SecondTitle"
              :isVipFree="item.isVipFree ?? false"
              :freeType="item.freeType ?? false"
              :value="inputvalue"
              :type="6"
            ></SearchItem>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SearchItem from "@/components/SearchItem.vue";
import PoemBook from "@/components/PoemBook.vue";
export default {
  name: "SearchSection",
  data() {
    return {
      inputvalue: this.$route.query.value,
      searchList: [],
    };
  },
  components: { SearchItem, PoemBook },
  created() {
   this.$nextTick(()=>{
     this.getSearchData();
   })
  },
  methods: {
    searchMoreList(num) {
        this.$router.push({
            name:"MoreSearchList",
            params:{
                type:num,
                value:this.inputvalue
            }
        })
    },
    async getSearchData() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/SearchAll",
        data: {
          str: this.inputvalue,
        },
        header: {
          "Content-Type": "application/x-www-form-urlencoded",
        },
      });
      this.searchList = data.data.data;
    },
    startSearch() {
      this.searchListShow = false;
      if (this.historySearchData.indexOf(this.inputvalue) == -1) {
        this.historySearchData.unshift(this.inputvalue);
      } else {
        this.historySearchData = this.historySearchData.filter((item) => {
          if (item == this.inputvalue) {
            return false;
          } else {
            return true;
          }
        });
        this.historySearchData.unshift(this.inputvalue);
      }

      localStorage.setItem(
        "historySearchData",
        JSON.stringify(this.historySearchData)
      );
      if (localStorage.historySearchData != null) {
        this.historySearchData = JSON.parse(
          localStorage.getItem("historySearchData")
        );
      }
      this.routertoSearchSection();
    },
    setSearchListShow(){
        this.getSearchData()
    },
    gohome() {
      this.$router.back(-1);
    },
  },
};
</script>

<style lang="scss" scoped>
#search-section {
  header {
    height: 50px;
    z-index: 999;
    position: sticky;
    top: 0;
    left: 0;
    background-color: #fff;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 0 20px;
    img {
      width: 10px;
    }
    form {
      flex: 1;
      font-size: 20px;
    }
  }
  .section {
    padding-bottom: 40px;
    .search-list-show {
      .search-list-item {
        border-top: 5px solid var(--theme-french-gray);
        &:first-child {
          border: 0px;
        }
        .search-item-title {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 20px 20px 5px;
          h3 {
            font-family: fzqk;
          }
          span {
            font-size: 12px;
            color: var(--theme-gray);
            img {
              margin-left: 5px;
              width: 5px;
            }
          }
        }

        .searhc-item-poem {
          margin: 0 20px;
          padding-top: 20px;
          border-top: 1px solid var(--theme-title-bottom);
        }
      }
    }
  }
}
</style>