<template>
  <div class="poem-book" @click="routerPoemPage">
    <div class="poem-book-item">
      <span>{{ itemindex }}</span>
      <div>
        <h3>{{ title }}</h3>
        <p>作者:{{ author }}</p>
      </div>
      <p>{{ SecondTitle }}</p>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    id: Number,
    value: [Number, String],
    title: String,
    author: String,
    SecondTitle: String,
    itemindex: [Number, String],
    type: Number,
  },
  name: "PoemBook",
  methods: {
    routerPoemPage() {
      this.getClickLocalData()
      this.$router.push({
        name: "section",
        params: {
          type: this.type,
          value: this.value,
          id: this.id,
        },
      });
    },
        getClickLocalData(){
       let obj = {
        type: this.type,
        value: this.value,
        id: this.id,
      };
       if (localStorage.clickItemData == undefined) {
        localStorage.setItem("clickItemData", JSON.stringify(obj));
      } else {
           localStorage.setItem("clickItemData", JSON.stringify(obj))
      }
    }
  },
};
</script>
<style lang="scss" scoped>
.poem-book {
  padding-bottom: 15px;
  .poem-book-item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    span {
      margin-left: 10px;
      width: max-content;
      color: orange;
    }
    div {
      flex: 1;
      margin: 0 10px;
      h3 {
        font-weight: 500;
        font-size: 15px;
      }
      p {
        width: 100%;
        margin-top: 5px;
        font-size: 13px;
        color: var(--theme-gray);
      }
    }
    p {
      margin-right: 10px;
      width: 60px;
      font-size: 12px;
      color: var(--theme-gray);
    }
  }
}
</style>